<template>
    <el-dialog title="添加商品类别" :visible="goodsTypeVisible" @update:visible="$emit('update:goodsTypeVisible',false)">
        <el-form :model="form" ref="goodsForm">
            <el-form-item label="商品类别名称">
                <el-input v-model="form.goodsTypeName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="店铺">
                <el-select  @change="changeShopTypeId"  v-model="form.shopTypeId" placeholder="请选择">
                    <el-option
                            key="0"
                            label="请选择"
                            value="0"
                    ></el-option>
                    <el-option
                            v-for="item in $store.state.shop.allShopTypeList"
                            :key="item._id"
                            :label="item.shopTypeName"
                            :value="item._id"
                    >
                    </el-option>
                </el-select>
                <el-select v-model="form.shopId" placeholder="请选择">
                    <el-option
                            key="0"
                            label="请选择"
                            value="0"
                    ></el-option>
                    <el-option
                            v-for="item in $store.state.shop.shopListByType"
                            :key="item._id"
                            :label="item.shopName"
                            :value="item._id"
                    >
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="noneGoods">取 消</el-button>
            <el-button @click="addGoodsType">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "add-goods-type",
        props:["goodsTypeVisible","shopTypeId","shopId"],
        data(){
            return{
                form:{
                    goodsTypeName:"",
                    shopId:"",
                    shopTypeId:"店铺类别",
                    shopId:"店铺名称",
                },
            }
        },
        created(){
            if(this.shopTypeId){
                this.form.shopTypeId = this.shopTypeId;
                console.log(this.shopTypeId)
                this.changeShopTypeId()
            }
            if(this.shopId){
                this.form.shopId = this.shopId
            }
        },
        methods:{
            changeShopTypeId(){
                this.form.shopId = "请选择",//初始化数据，二级选择后恢复初始值
                console.log(111111,this.shopTypeId)
                this.$store.dispatch("getShopListByType",this.form.shopTypeId)
            },
            addGoodsType(){
                this.$emit("update:goodsTypeVisible",false)
                this.$store.dispatch("addGoodsTypeList",this)
                this.$store.dispatch("getGoodsTypeList")//数据更新

            },
            noneGoods(){
                this.$emit("update:goodsTypeVisible",false)
            }
        },

    }
</script>

